.room {
      background: #ffffff;
      color: #333;
      border: 1px solid #ddd;
      border-radius: 8px;
      padding: 10px 20px;
      margin-bottom: 10px;
      text-align: center;
      font-size: 16px;
      cursor: pointer;
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  }
  
.room:hover {
      background: #f0f0f0;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }

.room .deleteButton {
  margin-left: 10px;
}