:global {
    
    .confirmModal {
        border: 1px solid black;
        width: 300px;
        height: 100px;
        border-radius: 12px;
        background-color: #b7b7b7;
        position: absolute;
        padding: 20px;
        text-align: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

   
}