.card
    background-color: lightsalmon

    h1
        color: white
        font-size: 1.5rem
        text-align: right

.title
    font-size: 1.5rem
    text-align: center
    margin-bottom: 1rem
.red-bg
    background-color: red
    color: white
    padding: 1rem
    border-radius: 0.5rem

    body
        padding-top: 70px
.hide
    display: none !important

.background
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100vh
    background: url(img/desk.jpg)
    // background: url(./public/img/desk.jpg)
    z-index: -1

.transformTooltip
    display: flex
    width: 320px
    position: absolute
    background-color: #b7b7b7
    height: 80px
    align-items: center
    bottom: 0
.tooltipBtn
    box-sizing: border-box
    width: 100%
    height: 80px
    padding: 10px 0
    &:hover
        background-color: #adadad
    img
        display: block
        margin: 0 auto
        pointer-events: none

.reviewBtns
    width: 70%
    display: flex
    margin: 0 auto
    gap: 10px
    .btn
        box-sizing: border-box
        text-align: center
        padding: 14px
        width: 50px
        height: 50px
        background-color: #8b8b8b
        border-radius: 12px
        border: 1px solid black
        img
            width: 100%
            height: auto
        &:hover
            background-color: #606060
        &.blocked
            pointer-events: none
            background-color: #c1c1c1

    
.confirmModal
    border: 1px solid black
    width: 300px
    height: 100px
    border-radius: 12px
    background-color: #b7b7b7
    position: absolute
    padding: 20px
    text-align: center
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    .confirmAnswers
        display: flex
        justify-content: space-around
        .btn
            border: 1px solid black
            border-radius: 4px
            width: 50px
            padding: 10px 0
            &.yesBtn
                background-color: #00FF00
                &:hover
                    background-color: #00c700
                &:active
                    background-color: #01d201
            &.noBtn
                background-color: #f00
                &:hover
                    background-color: #c70000
                &:active
                    background-color: #d20101

.absoluteWrapper
    position: absolute
    top: 10px
    left: 15px
    width: 200px

.turn
    text-align: center
    position: relative
    width: 100%
    padding: 15px 10px
    border-radius: 5px
    background-color: #b7b7b7
    margin-bottom: 10px
    &Span
        font-weight: bold
    &Span.white
        color: white
    &Span.black
        color: black
.history
    width: 100%
    padding: 15px 10px
    border-radius: 5px
    background-color: #b7b7b7
    .historyPoint
        margin-bottom: 5px
        display: flex
        align-items: center
        font-size: 24px
        .figureImg
            width: 30px
            height: 30px
            &.white
                filter: invert(1)

.boardWrapper
    width: 640px

.gameWrapper
    display: flex
    width: 100%

.menuWrapper
    padding: 24px
    background-color: #b7b7b7
    border-radius: 12px
    width: 30%
    h3
        text-align: center
    .newGameBtn
        width: 50%
        border: 1px solid black
        padding: 10px 20px
        margin: 0 auto
        text-align: center
        background-color: #a8a8a8
        border-radius: 10px
        margin-bottom: 30px
        &:hover
            background-color: #999999
    .emptySaveCage
        box-sizing: border-box
        height: 64px
        margin: 0 auto
        border: 1px solid black
        padding: 20px 0
        background-color: #a8a8a8
        border-radius: 10px
        margin-bottom: 10px
        text-align: center
        &:hover
            background-color: #999999
    .save
        position: relative
        box-sizing: border-box
        height: 64px
        margin: 0 auto
        margin-bottom: 10px
        border: 1px solid black
        padding: 10px 20px
        background-color: #a8a8a8
        border-radius: 10px
        display: flex
        align-items: center
        justify-content: space-between
        &:hover
            background-color: #999999
        .id
            font-size: 24px
        .deleteSaveBtn
            border-radius: 8px
            width: 32px
            height: 32px
            border: 1px solid black
            background-color: #9a3939
            img
                width: 100%
                height: auto
            &:hover
                background-color: #612121
        &.readOnly img.readOnlyIcon
            width: 30px
            height: auto
            position: absolute
            top: 50%
            right: 70%
            transform: translate(0, -50%)

.content
    display: flex
    align-items: center
    justify-content: space-between
    position: relative
    width: 60%
    height: 80vh
    transition: 0.3s all ease
    display: flex
    margin: 0 auto
    color: white

.player
    position: absolute
    width: 500px
    height: 50px
    border-radius: 12px
    background-color: #e5e5e5
    color: black
    padding: 12px
    box-sizing: border-box
    border: 1px solid black
    .score
        position: absolute
        right: 20px
        top: 50%
        transform: translate(0, -50%)
    .btn
        display: inline-block
        width: 20px
        height: 20px
        border: 0.5px solid black
        border-radius: 4px
        box-sizing: border-box
        text-align: center
        &.exitBtn
            background-color: #ff0000
        &.changeBtn
            background-color: #40ff00

    &.black
        top: -50px
    &.white
        bottom: -50px

.endGameModal
    height: 80%
    width: 50%
    border-radius: 16px
    padding: 30px
    text-align: center
    background-color: #b7b7b7
    h3
        font-size: 36px
    p
        font-size: 24px
    .btn
        width: 50%
        padding: 10px
        margin: 0 auto
        margin-bottom: 15px
        border: 1px solid black
        border-radius: 8px
        background-color: #a8a8a8
    &:hover
        background-color: #8f8d8d

#content.flip
    .topIndex, .botIndex
        flex-direction: row-reverse
    .leftIndex, .rightIndex
        flex-direction: column-reverse
    .chessDesk
        transform: rotate(180deg)
        .figure
            box-sizing: border-box
            padding-top: 10px
            transform: rotate(180deg)
            &.active
                top: 5px

.chessDesk
    transition: all 0.3s ease
    border: 1px solid black
    width: 640px
    height: 640px
    display: grid
    grid-template-columns: repeat(8, 1fr)
    grid-template-rows: repeat(8, 1fr)

    .line:nth-child(2n)
        .chessDeskCage:nth-child(2n)
            background-color: #616161
    .line:nth-child(2n - 1)
        .chessDeskCage:nth-child(2n-1)
            background-color: #616161

    .chessDeskCage
        width: 80px
        height: 80px
        box-sizing: border-box
        background-color: #b7b7b7

        &.moveHighlight
            box-shadow: 0 0 101px 100px rgba(0, 255, 19, 0.28) inset
        &.takeHighlight
            box-shadow: 0 0 101px 100px rgba(255, 0, 19, 0.28) inset

        @for $line from 1 through 8
            @for $col from 1 through 8
                @if ($line % 2 == 0)
                    @if($col % 2 != 0)
                        &:nth-child(#{$col + (8 * ($line - 1))})
                            background-color: #616161
                @else
                    @if($col % 2 == 0)
                        &:nth-child(#{$col + (8 * ($line - 1))})
                            background-color: #616161
        .figure, .reviewFigure
            width: 100%
            height: 100%
            padding-top: 10px
        img
            margin: 0 auto
            display: block
        .figure:global(.active), .reviewFigure.active
            position: relative
            bottom: 5px
        .figure.white, .reviewFigure.white
            filter: invert(1)

.topIndex
    width: 100%
    display: flex
    justify-items: space-between
    padding: 5px 0
    flex-direction: row
    &Letter
        width: 82px
        text-align: center

.botIndex
    width: 100%
    display: flex
    justify-items: space-between
    flex-direction: row
    &Letter
        width: 82px
        text-align: center
        padding: 5px 0

.leftIndex
    display: flex
    flex-direction: column
    padding: 28px 5px
    &Num
        height: 80px
        line-height: 80px

.rightIndex
    display: flex
    flex-direction: column
    padding: 28px 5px
    &Num
        height: 80px
        line-height: 80px
